.gradient-text {
  background: linear-gradient(135deg, #7C3AED, #EC4899);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
.btn-primary {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  background: #7C3AED;
  color: #fff;
  text-decoration: none;
  padding: 16px 32px;
  border-radius: 980px;
  font-size: 17px;
  font-weight: 600;
  transition: all 0.3s ease;
  box-shadow: 0 4px 14px rgba(124, 58, 237, 0.4);
}
.btn-primary:hover {
  background: #6D28D9;
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(124, 58, 237, 0.5);
}
.btn-secondary {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  background: #fff;
  color: #1d1d1f;
  text-decoration: none;
  padding: 16px 32px;
  border-radius: 980px;
  font-size: 17px;
  font-weight: 600;
  border: 1px solid #d2d2d7;
  transition: all 0.3s ease;
}
.btn-secondary:hover {
  background: #f5f5f7;
  border-color: #86868b;
}
.section-label {
  display: inline-block;
  font-size: 14px;
  font-weight: 600;
  color: #7C3AED;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  margin-bottom: 16px;
}
.feature-card {
  background: #f5f5f7;
  border-radius: 24px;
  padding: 40px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}
.feature-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.08);
}
.info-card {
  background: #f5f5f7;
  border-radius: 18px;
  padding: 32px;
  margin: 32px 0;
}
.contact-card {
  background: linear-gradient(135deg, #7C3AED 0%, #EC4899 100%);
  border-radius: 24px;
  padding: 48px;
  text-align: center;
  color: #fff;
}
@media (prefers-reduced-motion: no-preference) {
  .animate-fade-in {
    animation: fadeInUp 0.6s ease forwards;
  }
  @keyframes fadeInUp {
    from {
      opacity: 0;
      transform: translateY(20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
}
